.sign-in {
  padding: 100px 0 150px;

  &__form-wrapper {
    margin: 0 auto;
    border-radius: 10px;
    padding: 40px;
    width: 100%;
    max-width: 600px;
    background-color: var(--blue-color);
  }

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 500;
    font-size: 30px;
    line-height: 120%;
    color: var(--light-color);
  }

  &__form {
    @include flex;

    flex-direction: column;
    gap: 30px;
  }

  &__form-inputs {
    @include flex;

    flex-direction: column;
    gap: 15px;
  }

  &__label {
    position: relative;


    &::before {
      @include bg-cover;

      content: "";
      position: absolute;
      left: 20px;
      top: 50%;
      width: 30px;
      height: 30px;
      transform: translateY(-50%);
    }

    &--user {
      &::before {
        background-image: url("../img/sign/sign-user.svg");
      }


    }

    &--password {
      &::before {
        background-image: url("../img/sign/sign-password.svg");
      }
    }
  }

  &__input {
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 10px;
    padding: 16px 20px;
    padding-left: 60px;
    width: 100%;
    background-color: transparent;

    &,
    &::placeholder {
      font-weight: 400;
      font-size: 18px;
      line-height: 21px;
      color: #919BB5;
    }
  }

  &__subtitle {
    position: relative;
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 150%;
    color: rgba(255, 255, 255, 0.5);
    gap: 10px;

    @include flex-v-center;

    &::before,
    &::after {
      content: "";
      flex-grow: 1;
      border-radius: 1px;
      height: 1px;
      background-color: rgba(255, 255, 255, 50%);
    }
  }

  &__links {
    @include flex-all-sb;

    gap: 15px;
  }

  &__link {
    font-weight: 700;
    font-size: 18px;
    line-height: 150%;
    color: var(--light-color);
  }

  &__btn {
    border-radius: 10px;
    padding: 16px 28px;
    font-weight: 700;
    font-size: 24px;
    line-height: 28px;
    color: var(--light-color);
    background: var(--main-color);
  }
}
