.news {
  padding: 100px 0 150px;

  &-full {
    &__content {
      margin-bottom: 60px;
    }

    &__img {
      margin-bottom: 25px;
      object-fit: cover;
    }

    &__title {
      margin: 0;
      margin-bottom: 16px;
      font-weight: 700;
      font-size: 30px;
      line-height: normal;
      color: var(--dark-color)
    }

    &__date {
      margin-bottom: 10px;
      font-size: 14px;
      line-height: 21px;
      color: #b8b8b8;
    }

    &__descr {
      font-size: 16px;
      line-height: 26px;
      color: #655c6e;
    }
  }

  &__title-wrapper {
    @include flex-all-sb;

    margin-bottom: 60px;
    gap: 15px;

  }

  &__title {
    margin: 0;
    font-weight: 700;
    font-size: 45px;
    color: var(--dark-color);
  }

  &__link {
    position: relative;
    padding-right: 35px;
    font-weight: 700;
    font-size: 20px;
    color: #861F41;

    &::after {
      @include bg-cover;

      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      width: 25px;
      height: 18px;
      background-image: url("../img/news/news-arrow.svg");
      transform: translateY(-50%);
    }

    &--full {
      padding-right: initial;
      padding-left: 35px;

      &::after {
        left: 0;
        right: initial;
        transform: translateY(-50%) rotate(180deg);
      }
    }

  }

  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 30px;

    &--full {
      grid-template-columns: repeat(2, 1fr);
      margin-bottom: 60px;
    }
  }

  &__item {
    @include flex;

    flex-direction: column;
    gap: 20px;
  }

  &__item-img {
  }

  &__item-title {
    margin: 0;
    font-weight: 700;
    font-size: 25px;
    line-height: 125%;
    color: var(--dark-color);

  }

  &__item-descr {
    margin: 0;
    font-weight: 700;
    font-size: 16px;
    line-height: 125%;
    color: rgba(32, 32, 32, 0.5);
  }

  &__pagination {
    @include flex-all-center;

    gap: 50px;
  }

  &__pagination-link {
    font-weight: 700;
    font-size: 23px;
    color: var(--dark-color);
    opacity: 0.7;

    &--active {
      color: var(--main-color);
      opacity: 1;
    }
  }
}
