:root {
  --font-family: "Roboto", sans-serif;
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --light-color: #fff;
  --dark-color: #202020;
  --blue-color: #322243;
  --main-color: #861F41;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable */
.custom-checkbox__field:checked + .custom-checkbox__content::after {
  opacity: 1;
}

.custom-checkbox__field:focus + .custom-checkbox__content::before {
  outline: 2px solid #f00;
  outline-offset: 2px;
}

.custom-checkbox__field:disabled + .custom-checkbox__content {
  opacity: 0.4;
  pointer-events: none;
}

/* stylelint-disable */
/* stylelint-disable */
/* stylelint-disable value-keyword-case */
@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/../fonts/Roboto-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 360px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.header {
  background-color: var(--main-color);
}

.nav__content {
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  padding: 28px 0 20px;
}
.nav__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
}
.nav__btns-wrapper {
  display: flex;
  gap: 15px;
}
.nav__btn {
  border-radius: 10px;
  padding: 12px 25px;
  font-weight: 600;
  font-size: 16px;
  color: var(--light-color);
  color: #FDFDFD;
  background-color: var(--blue-color);
}
.nav__btn--icon {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 12px 20px;
  width: 100%;
  max-width: 70px;
}
.nav__btn--icon svg {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.nav__sub-content {
  padding: 20px 0 28px;
}
.nav__link {
  font-weight: 500;
  font-size: 18px;
  line-height: 25px;
  color: #FDFDFD;
}
.nav__list {
  display: flex;
  align-items: center;
  gap: 24px;
}

.hero .swiper-slide {
  position: relative;
  padding: 140px 0 300px;
}
.hero .swiper-button-prev,
.hero .swiper-button-next {
  top: 50%;
  transform: translateY(-50%);
}
.hero .swiper-button-prev::after,
.hero .swiper-button-next::after {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  content: "";
  width: 80px;
  height: 65px;
}
.hero .swiper-button-prev {
  left: 0;
}
.hero .swiper-button-prev::after {
  background-image: url("../img/hero/hero-prev.svg");
}
.hero .swiper-button-next {
  right: 0;
}
.hero .swiper-button-next::after {
  background-image: url("../img/hero/hero-next.svg");
}
.hero .swiper-pagination-bullet {
  width: 18px;
  height: 18px;
  background-color: var(--light-color);
}
.hero .swiper-pagination-bullet-active {
  background-color: var(--main-color);
}
.hero__slide-bg {
  position: absolute;
  left: 0;
  top: 0;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.hero__slide-content {
  flex-direction: column;
  align-items: flex-start;
  width: 100%;
  max-width: 1100px;
  display: flex;
}
.hero__title {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 59px;
  color: var(--light-color);
}
.hero__descr {
  margin: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 40px;
  color: var(--light-color);
}
.hero__link {
  border-radius: 10px;
  padding: 24px 34px;
  font-weight: 600;
  font-size: 35px;
  color: #FDFDFD;
  background-color: var(--main-color);
}
.hero__swiper-nav {
  position: absolute;
  left: 50%;
  bottom: 100px;
  z-index: 1;
  width: 100%;
  max-width: 1280px;
  transform: translateX(-50%);
}

.news {
  padding: 100px 0 150px;
}
.news-full__content {
  margin-bottom: 60px;
}
.news-full__img {
  margin-bottom: 25px;
  object-fit: cover;
}
.news-full__title {
  margin: 0;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 30px;
  line-height: normal;
  color: var(--dark-color);
}
.news-full__date {
  margin-bottom: 10px;
  font-size: 14px;
  line-height: 21px;
  color: #b8b8b8;
}
.news-full__descr {
  font-size: 16px;
  line-height: 26px;
  color: #655c6e;
}
.news__title-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 60px;
  gap: 15px;
}
.news__title {
  margin: 0;
  font-weight: 700;
  font-size: 45px;
  color: var(--dark-color);
}
.news__link {
  position: relative;
  padding-right: 35px;
  font-weight: 700;
  font-size: 20px;
  color: #861F41;
}
.news__link::after {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  right: 0;
  top: 50%;
  width: 25px;
  height: 18px;
  background-image: url("../img/news/news-arrow.svg");
  transform: translateY(-50%);
}
.news__link--full {
  padding-right: initial;
  padding-left: 35px;
}
.news__link--full::after {
  left: 0;
  right: initial;
  transform: translateY(-50%) rotate(180deg);
}
.news__list {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px;
}
.news__list--full {
  grid-template-columns: repeat(2, 1fr);
  margin-bottom: 60px;
}
.news__item {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.news__item-title {
  margin: 0;
  font-weight: 700;
  font-size: 25px;
  line-height: 125%;
  color: var(--dark-color);
}
.news__item-descr {
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 125%;
  color: rgba(32, 32, 32, 0.5);
}
.news__pagination {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
}
.news__pagination-link {
  font-weight: 700;
  font-size: 23px;
  color: var(--dark-color);
  opacity: 0.7;
}
.news__pagination-link--active {
  color: var(--main-color);
  opacity: 1;
}

.sign-in {
  padding: 100px 0 150px;
}
.sign-in__form-wrapper {
  margin: 0 auto;
  border-radius: 10px;
  padding: 40px;
  width: 100%;
  max-width: 600px;
  background-color: var(--blue-color);
}
.sign-in__title {
  margin: 0;
  margin-bottom: 30px;
  font-weight: 500;
  font-size: 30px;
  line-height: 120%;
  color: var(--light-color);
}
.sign-in__form {
  display: flex;
  flex-direction: column;
  gap: 30px;
}
.sign-in__form-inputs {
  display: flex;
  flex-direction: column;
  gap: 15px;
}
.sign-in__label {
  position: relative;
}
.sign-in__label::before {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  content: "";
  position: absolute;
  left: 20px;
  top: 50%;
  width: 30px;
  height: 30px;
  transform: translateY(-50%);
}
.sign-in__label--user::before {
  background-image: url("../img/sign/sign-user.svg");
}
.sign-in__label--password::before {
  background-image: url("../img/sign/sign-password.svg");
}
.sign-in__input {
  border: 1px solid rgba(255, 255, 255, 0.5);
  border-radius: 10px;
  padding: 16px 20px;
  padding-left: 60px;
  width: 100%;
  background-color: transparent;
}
.sign-in__input, .sign-in__input::placeholder {
  font-weight: 400;
  font-size: 18px;
  line-height: 21px;
  color: #919BB5;
}
.sign-in__subtitle {
  position: relative;
  margin: 0;
  font-weight: 700;
  font-size: 16px;
  line-height: 150%;
  color: rgba(255, 255, 255, 0.5);
  gap: 10px;
  display: flex;
  align-items: center;
}
.sign-in__subtitle::before, .sign-in__subtitle::after {
  content: "";
  flex-grow: 1;
  border-radius: 1px;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.5);
}
.sign-in__links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 15px;
}
.sign-in__link {
  font-weight: 700;
  font-size: 18px;
  line-height: 150%;
  color: var(--light-color);
}
.sign-in__btn {
  border-radius: 10px;
  padding: 16px 28px;
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: var(--light-color);
  background: var(--main-color);
}

.footer {
  padding: 40px 0;
  background-color: var(--blue-color);
}
.footer__content {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 30px;
}
.footer__lang {
  display: flex;
  align-items: center;
  border: 1px solid rgba(255, 255, 255, 0.4);
  border-radius: 10px;
  padding: 10px 15px;
  gap: 19px;
}
.footer__btn {
  color: var(--light-color);
}
.footer__btn svg {
  display: block;
  width: 30px;
  height: 21px;
}
.footer__list {
  display: flex;
  gap: 70px;
}
.footer__item {
  flex-direction: column;
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: rgba(253, 253, 253, 0.5);
  gap: 13px;
  display: flex;
}
.footer__item-text {
  font-weight: 600;
  font-size: 18px;
  line-height: 25px;
  color: #FDFDFD;
}
.footer__copyright {
  margin: 0 auto;
  width: 100%;
  max-width: 680px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  text-align: center;
  color: rgba(255, 255, 255, 0.5);
}