.nav {
  &__content {
    border-bottom: 1px solid rgba(255, 255, 255, 0.4);
    padding: 28px 0 20px;
  }

  &__container {
    @include flex-all-sb;

    gap: 15px;
  }

  &__btns-wrapper {
    @include flex;

    gap: 15px;
  }

  &__btn {
    border-radius: 10px;
    padding: 12px 25px;
    font-weight: 600;
    font-size: 16px;
    color: var(--light-color);
    color: #FDFDFD;
    background-color: var(--blue-color);

    &--icon {
      @include flex-all-center;

      padding: 12px 20px;
      width: 100%;
      max-width: 70px;


      svg {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }

  &__sub-content {
    padding: 20px 0 28px;
  }

  &__link {
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    color: #FDFDFD;
  }

  &__list {
    @include flex-v-center;

    gap: 24px;
  }
}
