.hero {
  .swiper-slide {
    position: relative;
    padding: 140px 0 300px;
  }

  .swiper-button-prev,
  .swiper-button-next {
    top: 50%;
    transform: translateY(-50%);

    &::after {
      @include bg-cover;

      content: "";
      width: 80px;
      height: 65px;
    }
  }

  .swiper-button-prev {
    left: 0;

    &::after {
      background-image: url("../img/hero/hero-prev.svg");
    }
  }

  .swiper-button-next {
    right: 0;

    &::after {
      background-image: url("../img/hero/hero-next.svg");
    }
  }

  .swiper-pagination-bullet {
    width: 18px;
    height: 18px;
    background-color: var(--light-color);
  }

  .swiper-pagination-bullet-active {
    background-color: var(--main-color);
  }

  &__slide-bg {
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    width: 100%;
    height: 100%;
  }

  &__slide-content {
    flex-direction: column;
    align-items: flex-start;
    width: 100%;
    max-width: 1100px;

    @include flex;
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 59px;
    color: var(--light-color);
  }

  &__descr {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 40px;
    color: var(--light-color);
  }

  &__link {
    border-radius: 10px;
    padding: 24px 34px;
    font-weight: 600;
    font-size: 35px;
    color: #FDFDFD;
    background-color: var(--main-color);
  }

  &__swiper-nav {
    position: absolute;
    left: 50%;
    bottom: 100px;
    z-index: 1;
    width: 100%;
    max-width: 1280px;
    transform: translateX(-50%);
  }


}
