.footer {
  padding: 40px 0;
  background-color: var(--blue-color);

  &__content {
    @include flex-v-center;

    flex-direction: column;
    gap: 30px;
  }

  &__lang {
    @include flex-v-center;

    border: 1px solid rgba(255, 255, 255, 0.4);
    border-radius: 10px;
    padding: 10px 15px;
    gap: 19px;
  }

  &__btn {
    color: var(--light-color);

    svg {
      display: block;
      width: 30px;
      height: 21px;
    }
  }

  &__list {
    @include flex;

    gap: 70px;
  }

  &__item {
    flex-direction: column;
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: rgba(253, 253, 253, 0.5);
    gap: 13px;

    @include flex;
  }

  &__item-text {
    font-weight: 600;
    font-size: 18px;
    line-height: 25px;
    color: #FDFDFD;
  }

  &__copyright {
    margin: 0 auto;
    width: 100%;
    max-width: 680px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: rgba(255, 255, 255, 0.5);
  }
}
